import React from "react"
import ReactMarkdown from "react-markdown"
import RemarkGfm from "remark-gfm"

const markdown = `   
# Pre-requisites

There are no special hardware requirements for building a MiniDapp.

You will need:

A laptop or server with the command line interface open
A code editor, 
for example **[VS Code](https://code.visualstudio.com/)**,

and [Java](https://www.java.com/en/download/) installed

_To check if you already have Java installed type **java -version**
in your Terminal/CLI_

The [minima.jar](https://github.com/minima-global/Minima/releases) java file downloaded

# Start a single test node

Starting a node with -genesis will start your own private test chain from
the genesis (first) block with 1 billion coins to spend.

- Create a new folder called minima and copy the minima.jar into that
  folder.
- Copy the path to the folder
- Open a new command line interface and cd
  into that folder 
- Enter the following:

~~~~
java -jar minima.jar -data minidata1 -test -nop2p -genesis
~~~~

Replace **INSERT PASSWORD** with a custom password for the test node.

You will notice the minidata folder has been created in your minima folder. This
will start a node on the default ports of 9001-5.

You should see the node starting up. You can type commands directly into the console, try entering **balance** to check your balance.

- Type **help** for the full list of Minima commands.
- Access your MiniDapp hub by
  going to **https://127.0.0.1:9003**
- Accept the security warning and login
  with the password you set above
  
**That's it, let's get started!**
`

const TutorialIntroduction = () => {
  return <ReactMarkdown children={markdown} remarkPlugins={[RemarkGfm]} />
}

export default TutorialIntroduction
