import { PaletteMode } from '@mui/material';
import { grey } from '@mui/material/colors';


const getDesignTokens = (mode: PaletteMode) => ({
  palette: {
    mode: mode,
    ...(mode === 'light' ? {
      primary: MinimaBlue,
      divider: '#EDEDED',
      text: {
        primary: grey[900],
        secondary: grey[800],
      },
      background: {
        default: '#FFFFFF',
        paper: '#FFFFFF'
      },

    } : {
      primary: MinimaBlack,
      divider: MinimaBlack[900],
      background: {
        default: MinimaBlack[900],
        paper: MinimaBlack[900],
      },
      text: {
        primary: MinimaBlack[900],
        secondary: grey[500],
      },
    })
  },
  typography: {
    fontFamily: [
      "Manrope-regular"
    ].join(','),
    mode,
    ...(mode === 'light' ? {
      body1: {
        fontFamily: 'Manrope-regular',
        color: MinimaBlack[900],
      }, 
      
    } : {
      body1: {
        fontFamily: 'Manrope-regular',
        color: '#FFFFFF'
      },
      
    })
  },
  components: {
    MuiButton: {
      mode,
      ...(mode === 'light' ? {
        styleOverrides: {
          root: {
            border: "2px solid #fff",
            borderColor: '#EDEDED',
            borderRadius: 8,
            backgroundColor: MinimaGrey[50],
            color: MinimaBlack[900],
            padding: "14px 33px",
            lineHeight: "140%",
            fontSize: "18px",
            ":hover": {
              backgroundColor: MinimaGrey[100],
              color: MinimaBlack[900],
              transform: "scale(0.96)",
            },
            fontFamily: "Manrope-bold",  
          }
        }
      } : {
        styleOverrides: {
          root: {
            border: "2px solid #fff",
            borderRadius: 8,
            backgroundColor: "transparent",
            padding: "14px 33px",
            color: "#fff",
            lineHeight: "140%",
            fontSize: "18px",
            ":hover": {
              backgroundColor: "#fff",
              color: "#16181C",
              transform: "scale(0.96)",
            },
            fontFamily: "Manrope-bold",  
          }
        }
      }),
    },
    MuiToolbar: {
      mode,
      ...(mode === 'light' ? 
      {
        styleOverrides: {
          root: {
            backgroundColor: MinimaGrey[50],
            borderBottom: '0.5px solid #EDEDED'
          }
        }
      }
        :
      {
        styleOverrides: {
          root: {
            backgroundColor: '#FFFFFF',
            borderBottom: 0
          }
        } 
      })
    },
    MuiListItemButton: {
      mode,
      ...(mode === 'light' ? {
        styleOverrides: {
          root: {
            paddingTop: 0,
            paddingBottom: 0,
            marginBottom: '5px',
            backgroundColor: 'transparent',
            lineHeight: 1.2,
            '&:hover': {
              backgroundColor: MinimaGrey[50],
            }
            
          }
        }
      } : {
        styleOverrides: {
          root: {
            paddingTop: 0,
            paddingBottom: 0,
            marginBottom: '5px',
            lineHeight: 1.2,
            '&:hover': {
              backgroundColor: '#25272b'
            }
            
          }
        }
      })
    },
    MuiAccordionDetails: {
      mode,
      ...(mode === 'light' ? {
        styleOverrides: {
          root: {
            color: '#FFFFFF',
            backgroundColor: '#FFFFFF'
          }
        }
      } : {
        styleOverrides: {
          root: {
            color: '#FFFFFF',
            backgroundColor: MinimaBlack[900]
          }
        }
      })
    },
    MuiAccordion: {
      styleOverrides: {
        root: {
          fontFamily: 'Manrope-bold',
          backgroundColor: 'transparent',
          boxShadow: 'none',
          zIndex: 0,
          borderRadius: '8px',
          marginBottom: '5px',
          border: 'none',
          outline: 'none',
          "&:before": {
            display: "none",
          },
        }
      }
    },
    MuiAccordionSummary: {
      mode,
      ...(mode === 'light' ? {
        styleOverrides: {
          root: {
            color: MinimaBlack[900],
            borderRadius: '8px',
            height: '24px!important',
            '&:hover': {
  
              '#expandIcon': {
                color: MinimaBlue[900]
              }
            }
          }
        }
      } : {
        styleOverrides: {
          root: {
            color: '#FFFFFF',
            borderRadius: '8px',
            backgroundColor: MinimaBlack[900],
            height: '24px!important',
            '&:hover': {
  
              '#expandIcon': {
                color: '#fff'
              }
            }
          }
        }
      })
      
    },
    MuiDrawer: {
      mode,
      ...(mode === 'light' ? {
        styleOverrides: {
          root: {
            backgroundColor: '#FFFFFF'
          }
        }
      } : {
        styleOverrides: {
          root: {
            backgroundColor: MinimaBlack[900]
          }
        }
      })
    },
  },
});

export { getDesignTokens }


