// header, footer, main content
// main content has the three butotn page
// WhitePaper with a drop down section listing
// yellowPaper with drop down section listing
// Transaction tutorial
// ?
const AppLayout = () => {
  const theme = useTheme()
  const colorMode = useContext(ColorModeContext)
  const location = useLocation()
  const navigate = useNavigate()
  const [open, setOpen] = useState(false)
  const currentYear = new Date().getFullYear()

  const handleCloseDrawerToggle = () => {
    return open ? setOpen(false) : null
  }

  const handleOpenDrawerToggle = () => {
    return !open ? setOpen(true) : null
  }

  return (
    // Which header toolbar shall we load?
    <div className="App">
      {location.pathname
        .substring(0, "/whitepaper/".length)
        .includes("/whitepaper/") ||
      location.pathname
        .substring(0, "/yellowpaper/".length)
        .includes("/yellowpaper/") ||
      location.pathname
        .substring(0, "/transactiontutorial/".length)
        .includes("/transactiontutorial/") ||
      location.pathname
        .substring(0, "/minidapptutorials/".length)
        .includes("/minidapptutorials/") ? (
        <Toolbar
          className="App-header App-header-papers"
          sx={{ textAlign: "left" }}
        >
          <Grid container>
            <Grid item lg={1} xs={0}></Grid>
            <Grid item lg={8} xs={12}>
              {location.pathname
                .substring(0, "/whitepaper/".length)
                .includes("/whitepaper/") ? (
                <ToolbarComponent
                  title="Whitepaper"
                  handleOpenDrawerToggle={handleOpenDrawerToggle}
                  navigate={navigate}
                />
              ) : location.pathname
                  .substring(0, "/yellowpaper/".length)
                  .includes("/yellowpaper/") ? (
                <ToolbarComponent
                  title="Yellowpaper"
                  handleOpenDrawerToggle={handleOpenDrawerToggle}
                  navigate={navigate}
                />
              ) : location.pathname
                  .substring(0, "/transactiontutorial/".length)
                  .includes("/transactiontutorial/") ? (
                <ToolbarComponent
                  title="Transaction Tutorial"
                  handleOpenDrawerToggle={handleOpenDrawerToggle}
                  navigate={navigate}
                />
              ) : location.pathname
                  .substring(0, "/minidapptutorials/".length)
                  .includes("/minidapptutorials/") ? (
                <ToolbarComponent
                  title="MiniDapp Tutorials"
                  handleOpenDrawerToggle={handleOpenDrawerToggle}
                  navigate={navigate}
                />
              ) : null}
            </Grid>
            <Grid item lg={2} xs={0}></Grid>
          </Grid>
        </Toolbar>
      ) : (
        <div />
      )}
      <main
        className={
          location.pathname
            .substring(0, "/whitepaper/".length)
            .includes("/whitepaper/") ||
          location.pathname
            .substring(0, "/yellowpaper/".length)
            .includes("/yellowpaper/") ||
          location.pathname
            .substring(0, "/transactiontutorial/".length)
            .includes("/transactiontutorial/") ||
          location.pathname
            .substring(0, "/minidapptutorials/".length)
            .includes("/minidapptutorials/")
            ? "App-content App-content-papers"
            : "App-content App-content-home"
        }
      >
        <Routes>
          <Route path="/" element={<HomePage />}></Route>
          <Route
            path="/whitepaper"
            element={
              <PaperContainer
                open={open}
                handleCloseDrawerToggle={handleCloseDrawerToggle}
              />
            }
          >
            <Route path="" element={<Navigate replace to="abstract" />} />
            <Route path="abstract" element={<Abstract />} />
            <Route path="introduction" element={<Introduction />} />
            <Route path="txpow" element={<TxPoW />} />
            <Route path="maxima" element={<Maxima />} />
            <Route path="pulsenetwork" element={<PulseNetwork />} />
            <Route path="pow" element={<PoW />} />
            <Route path="hashcash" element={<HashCash />} />
            <Route path="energy" element={<Energy />} />
            <Route path="cascadingchain" element={<CascadingChain />} />
            <Route path="storageless" element={<StorageLess />} />
            <Route path="smartcontracts" element={<SmartContracts />} />
            <Route path="quantumsecure" element={<QuantumSecure />} />
            <Route path="blocksize" element={<BlockSize />} />
            <Route path="blockspeed" element={<BlockSpeed />} />
            <Route path="consensus" element={<Consensus />} />
            <Route path="conclusion" element={<Conclusion />} />
            <Route path="specialthanks" element={<SpecialThanks />} />
            <Route path="*" element={<Navigate replace to="abstract" />} />
          </Route>
          <Route
            path="/yellowpaper"
            element={
              <PaperContainer
                open={open}
                handleCloseDrawerToggle={handleCloseDrawerToggle}
              />
            }
          >
            <Route
              path=""
              element={<Navigate replace to="networkoverview" />}
            />
            <Route path="networkoverview" element={<NetworkOverview />} />
            <Route path="coreconcepts" element={<CoreConcepts />} />
            <Route path="configuration" element={<Configuration />} />
            <Route path="transactions" element={<Transactions />} />
            <Route path="mmr" element={<MMR />} />
            <Route path="txpow" element={<TxPoWYellow />} />
            <Route path="theblockchain" element={<TheBlockchain />} />
            <Route path="miningandconsensus" element={<MiningAndConsensus />} />
            <Route path="quantumsecurity" element={<QuantumSecurity />} />
            <Route path="colouredcoins" element={<ColoredCoins />} />
            <Route path="scripting" element={<Scripting />} />
            <Route
              path="*"
              element={<Navigate replace to="networkoverview" />}
            />
          </Route>
          <Route
            path="/transactiontutorial"
            element={
              <PaperContainer
                open={open}
                handleCloseDrawerToggle={handleCloseDrawerToggle}
              />
            }
          >
            <Route path="" element={<Navigate replace to="introduction" />} />
            <Route path="introduction" element={<TutorialIntroduction />} />
            <Route path="pruning" element={<TutorialPruning />} />
            <Route
              path="basicsignedcontract"
              element={<BasicSignedContract />}
            />
            <Route path="timelockcontract" element={<TimeLockContract />} />
            <Route path="multisigcontract" element={<MultiSigContract />} />
            <Route
              path="morecomplexmultisig"
              element={<MoreComplexMultiSig />}
            />
            <Route path="mofnmultisigcontract" element={<MofNMultiSig />} />
            <Route path="slowcash" element={<SlowCash />} />
            <Route path="hashedtimelock" element={<HashedTimeLockContract />} />
            <Route path="exchangecontract" element={<ExchangeContract />} />
            <Route path="flashcash" element={<FlashCash />} />
            <Route path="multisigmulticoin" element={<MultiSigMultiCoin />} />
            <Route path="mastcontracts" element={<MASTContracts />} />
            <Route path="thevault" element={<TheVault />} />
            <Route path="coinflip" element={<CoinFlip />} />

            <Route path="unidirectional" element={<UniDirectional />} />
            <Route path="bidirectional" element={<BiDirectional />} />
            <Route path="eltoo" element={<ELTOO />} />
            <Route path="precoin" element={<PreCoin />} />
            <Route path="floatingcoin" element={<FloatingCoin />} />
            <Route path="eltoosequence" element={<ELTOOSequence />} />
            <Route path="statechains" element={<StateChains />} />
            <Route path="coinflipv2" element={<CoinFlipV2 />} />
            <Route path="tokens" element={<Tokens />} />
            <Route path="grammar" element={<Grammar />} />
            <Route path="globals" element={<Globals />} />
            <Route path="functions" element={<Functions />} />

            <Route path="*" element={<SectionNotFound />} />
          </Route>

          <Route
            path="/minidapptutorials"
            element={
              <PaperContainer
                open={open}
                handleCloseDrawerToggle={handleCloseDrawerToggle}
              />
            }
          >
            <Route path="" element={<Navigate replace to="introduction" />} />
            <Route path="introduction" element={<TutorialIntro />} />
            <Route path="tutorial-setup" element={<SetUp/>} />
            <Route path="tutorial-setup-single-node" element={<SetUpSingleNode />} />
            <Route path="hello-world" element={<HelloWorld />} />
            <Route path="simple-wallet" element={<SimpleWallet/>} />

            <Route path="ministats" element={<MiniStats />} />
            <Route path="*" element={<SectionNotFound />} />
          </Route>

          <Route path="*" element={<SectionNotFound />} />
        </Routes>
      </main>
      {/* Render footer? */}
      {location.pathname
        .substring(0, "/whitepaper/".length)
        .includes("/whitepaper/") ||
      location.pathname
        .substring(0, "/yellowpaper/".length)
        .includes("/yellowpaper/") ||
      location.pathname
        .substring(0, "/transactiontutorial/".length)
        .includes("/transactiontutorial/") ||
      location.pathname
        .substring(0, "/minidapptutorials/".length)
        .includes("/minidapptutorials/") ? null : (
        <footer
          className={`App-footer ${
            theme.palette.mode === "light" ? "App-footer-light" : ""
          }`}
        >
          <Grid container spacing={1} flexDirection="row">
            <Grid item xs={12} md={6} sx={{ paddingTop: "0 !important" }}>
              <Stack direction="column" spacing={1}>
                <MinimaLogo className="App-logo" />
                <Typography
                  sx={{
                    fontSize: "calc(8px + 1vmin)",
                  }}
                  variant="subtitle1"
                >
                  The evolution will not be centralized.
                </Typography>
                <Typography
                  sx={{
                    fontFamily: "Manrope-bold",
                    fontSize: "calc(8px + 1vmin)",
                    marginTop: "0 !important",
                  }}
                  variant="subtitle2"
                >
                  Become part of it.
                </Typography>
              </Stack>
            </Grid>
            <Grid
              item
              xs={12}
              md={6}
              sx={{
                display: "flex",
                alignItems: "start",
                justifyContent: "flex-end",
              }}
            >
              <Stack direction="row" spacing={1}>
                <IconButton onClick={colorMode.toggleColorMode} color="inherit">
                  {theme.palette.mode === "dark" ? (
                    <Brightness7Icon
                      sx={{ fontSize: "calc(16px + 2vmin)", opacity: 0.8 }}
                    />
                  ) : (
                    <Brightness4Icon
                      sx={{ fontSize: "calc(16px + 2vmin)", opacity: 0.8 }}
                    />
                  )}
                </IconButton>
              </Stack>
            </Grid>
          </Grid>
          <Grid container spacing={1} sx={{ paddingTop: "40px" }}>
            <Grid item xs={12} md={6}>
              <Typography
                sx={{ color: "#91919d", fontSize: "16px !important" }}
              >
                All rights reserved Minima GmbH &copy;{currentYear}.
              </Typography>
            </Grid>
          </Grid>
        </footer>
      )}
    </div>
  )
}
export default AppLayout

const ToolbarComponent = (props: any) => {
  return (
    <Box
      component="div"
      sx={{
        display: "flex",
        justifyContent: "space-between",
        flexDirection: "row",
      }}
    >
      <ToolBarTitle
        title={props.title}
        handleOpenDrawerToggle={props.handleOpenDrawerToggle}
      />

      <HomeButton navigate={props.navigate} />
    </Box>
  )
}

const ToolBarTitle = (props: any) => {
  return (
    <Box
      component="div"
      sx={{
        display: "flex",
        justifyContent: "flex-start",
        alignItems: "center",
      }}
    >
      <IconButton
        sx={{
          display: {
            xs: "flex",
            sm: "none",
          },
          paddingLeft: 0,
          alignItems: "center",
          justifyContent: "center",
          "&:hover": {
            backgroundColor: "transparent",
          },
        }}
        color="inherit"
        aria-label="menu"
        onClick={props.handleOpenDrawerToggle}
      >
        <MenuIcon />
      </IconButton>
      {props.title}
    </Box>
  )
}

const HomeButton = (props: any) => {
  return (
    <Box
      component="div"
      color="inherit"
      aria-label="home"
      sx={{
        display: "flex",
        justifyContent: "center",
        alignItems: "center",
        flexDirection: "column",
        "&:hover": {
          "#cottageIcon": {
            display: "block",
            cursor: "pointer",
          },
          "#homeIcon": {
            display: "none",
          },
        },
      }}
    >
      <IconButton
        onClick={() => props.navigate("/")}
        sx={{
          color: "#16181C",
          padding: 0,
          "&:hover": {
            backgroundColor: "transparent",
            transform: "scale(1.1)",
            cursor: "pointer",
          },
        }}
      >
        <HomeIcon id="homeIcon" sx={{ fontSize: "calc(16px + 1vmin)" }} />
        <CottageIcon sx={{ display: "none" }} id="cottageIcon" />
      </IconButton>
    </Box>
  )
}
