import { useState, useMemo, createContext } from "react";
import "./App.css";
import AppLayout from "./components/AppLayout";

import { createTheme, ThemeProvider } from "@mui/material/styles";
import { getDesignTokens } from "./theme/theme";
import { CssBaseline, PaletteMode } from "@mui/material";
import TitleBar from "./components/TitleBar";
// CssBaseline needed to get main, body to change color from theme
// import theme from "./theme/theme";

export const ColorModeContext = createContext({ toggleColorMode: () => {} });

const userPref: any =
  (localStorage.getItem("darkMode") &&
    localStorage.getItem("darkMode") === "light") ||
  localStorage.getItem("darkMode") === "dark"
    ? localStorage.getItem("darkMode")
    : "light";

function App() {
  // const prefersDarkMode = useMediaQuery("(prefers-color-scheme: dark)"); // we use a toggle instead
  // console.log(prefersDarkMode);
  // console.log("USERPREF", userPref);
  const [mode, setMode] = useState<PaletteMode>(
    userPref && userPref.length > 0 ? userPref : "light"
  );
  const colorMode = useMemo(
    () => ({
      // The dark mode switch would invoke this method
      toggleColorMode: () => {
        setMode((prevMode: PaletteMode) =>
          prevMode === "light" ? "dark" : "light"
        );
      },
    }),
    []
  );

  localStorage.setItem("darkMode", mode);

  const theme = useMemo(() => createTheme(getDesignTokens(mode)), [mode]);
  return (
    <ColorModeContext.Provider value={colorMode}>
      <ThemeProvider theme={theme}>
        <CssBaseline />
        <AppLayout />
      </ThemeProvider>
    </ColorModeContext.Provider>
  );
}

export default App;
