import {
  Accordion,
  AccordionDetails,
  AccordionSummary,
  Divider,
  Drawer,
  List,
  ListItemButton,
  ListItemText,
  Toolbar,
  Grid,
  Typography,
  Stack,
} from "@mui/material"
import { Box } from "@mui/system"

import { Outlet, useLocation, useNavigate } from "react-router-dom"

import ExpandMoreIcon from "@mui/icons-material/ExpandMore"
import MinimaLogoSquare from "../assets/icons/docs.png"

import { DrawerContentWP } from "./whitepaper"
import { DrawerContentYP } from "./yellowpaper"
import { DrawerContentTT } from "./transactiontutorial"
import { DrawerContentMT } from "./minidapptutorials"

const DefaultDrawerSize = 240

const PaperContainer = (props: any) => {
  let navigate = useNavigate()
  let location = useLocation()
  let mobileOpen = props.open
  let handleCloseDrawerToggle = props.handleCloseDrawerToggle
  const { window } = props
  const activeRoute = (routeName: string) => {
    return location.pathname.includes(routeName)
  }

  const CustomDrawerListItem = (props: any) => {
    return (
      <ListItemButton
        key={props.pathName + Math.random()}
        onClick={() => {
          navigate(`/${props.section}/${props.pathName}`)
          handleCloseDrawerToggle()
        }}
        selected={activeRoute(`/${props.pathName}`)}
      >
        <ListItemText key={props.pathName} primary={props.name} />
      </ListItemButton>
    )
  }

  const drawer = (
    <div>
      <Divider />
      <List>
        {location.pathname.includes("yellowpaper")
          ? DrawerContentYP.map((drawerItem) => (
              <CustomDrawerListItem
                key={drawerItem.pathName}
                name={drawerItem.name}
                pathName={drawerItem.pathName}
                section="yellowpaper"
              />
            ))
          : location.pathname.includes("/whitepaper")
          ? DrawerContentWP.map((drawerItem, index) => (
              <CustomDrawerListItem
                key={index}
                name={drawerItem.name}
                pathName={drawerItem.pathName}
                section="whitepaper"
              />
            ))
          : location.pathname.includes("/transactiontutorial")
          ? DrawerContentTT.map((drawerItem, index) => {
              if (drawerItem.children) {
                return (
                  <Accordion disableGutters={true} key={drawerItem.name}>
                    <AccordionSummary
                      expandIcon={
                        <ExpandMoreIcon
                          id="expandIcon"
                          sx={{ color: "#25272b" }}
                        />
                      }
                    >
                      <Typography variant="subtitle1">
                        {drawerItem.name}
                      </Typography>
                    </AccordionSummary>
                    <AccordionDetails>
                      {drawerItem.children.map((item, index) => (
                        <CustomDrawerListItem
                          key={index}
                          name={item.name}
                          pathName={item.pathName}
                          section="transactiontutorial"
                        />
                      ))}
                    </AccordionDetails>
                  </Accordion>
                )
              } else {
                return (
                  <CustomDrawerListItem
                    key={drawerItem.pathName}
                    name={drawerItem.name}
                    pathName={drawerItem.pathName}
                    section="transactiontutorial"
                  />
                )
              }
            })
          : location.pathname.includes("/minidapptutorials")
          ? DrawerContentMT.map((drawerItem, index) => {
            if (drawerItem.children) {
              return (
                <Accordion disableGutters={true} key={drawerItem.name}>
                  <AccordionSummary
                    expandIcon={
                      <ExpandMoreIcon
                        id="expandIcon"
                        sx={{ color: "#25272b" }}
                      />
                    }
                  >
                    <Typography variant="subtitle1">
                      {drawerItem.name}
                    </Typography>
                  </AccordionSummary>
                  <AccordionDetails>
                    {drawerItem.children.map((item, index) => (
                      <CustomDrawerListItem
                        key={index}
                        name={item.name}
                        pathName={item.pathName}
                        section="minidapptutorials"
                      />
                    ))}
                  </AccordionDetails>
                </Accordion>
              )
            } else {
              return (
                <CustomDrawerListItem
                  key={drawerItem.pathName}
                  name={drawerItem.name}
                  pathName={drawerItem.pathName}
                  section="minidapptutorials"
                />
              )
            }
          })
        : null}
      </List>
    </div>
  )

  const container =
    window !== undefined ? () => window().document.body : undefined

  return (
    <>
      <Box
        component="nav"
        sx={{ width: { sm: DefaultDrawerSize, flexShrink: { sm: 0 } } }}
        aria-label="mailbox folders"
      >
        {/* Mobile Drawer */}
        <Drawer
          onBackdropClick={handleCloseDrawerToggle}
          container={container}
          variant="temporary"
          ModalProps={{
            keepMounted: true, // Better open performance on mobile.
          }}
          open={mobileOpen}
          sx={{
            display: { xs: "block", sm: "none" },
            "& .MuiDrawer-paper": {
              boxSizing: "border-box",
              width: DefaultDrawerSize,
            },
          }}
        >
          {drawer}
        </Drawer>
        {/* Desktop Drawer */}
        <Drawer
          variant="permanent"
          sx={{
            display: { xs: "none", sm: "block" },
            "& .MuiDrawer-paper": {
              boxSizing: "border-box",
              width: DefaultDrawerSize,
            },
          }}
          open
        >
          {drawer}
        </Drawer>
      </Box>
      <Box component="main">
        <Grid container>
          <Grid item lg={1} xs={0} />
          <Grid item xs={12} md={12} lg={8}>
            <Outlet />
          </Grid>
        </Grid>
        <Grid item lg={2} xs={0} />
      </Box>
    </>
  )
}

export default PaperContainer
