import { useTheme } from "@mui/system";
import { PaletteMode } from "@mui/material";
const CustomImageRenderer = (props: any) => {
  const theme = useTheme();
  const mode = theme.palette.mode;

  let uri = convertUriDarkMode(props.node.properties.src, mode);
  return (
    <span className="CustomComp">
      <object data={uri} type="image/svg+xml"></object>
    </span>
  );
};

export default CustomImageRenderer;

/**
 * Switch for lightMode svgs or darkMode, get around for react markdown
 * @param uri
 * @param mode light | dark
 * @returns string
 */
const convertUriDarkMode = (uri: string, mode: PaletteMode) => {
  return mode.includes("light") ? uri.replace(/Dm/g, "Lm") : uri;
};
