import ReactMarkdown from "react-markdown";

const markdown = `
# Block Speed

The security of a transaction is not improved by a faster block speed, since the amount of security is a function of the cumulative PoW of the parent blocks (hash-rates being equal 1 BTC block is worth 4 LTC blocks, since LTC runs 4x faster). A faster chain does increase the granularity of information[[6]](/#/whitepaper/specialthanks) and allows a user to know sooner when their transaction is secure enough. The longer you wait, the more secure.

If the block speed is too fast you risk decoherence, when the chain cannot keep up with itself as too many blocks are found and multiple conflicting branches emerge, but if the block speed is too slow you ignore the benefits of higher information granularity. 

Block speed can be much faster on a GHOST[[7]](/#/whitepaper/specialthanks) chain and so Minima is aiming for a 50 second block time[[6]](/#/whitepaper/specialthanks).
`;

const BlockSpeed = () => {
  return <ReactMarkdown children={markdown} />;
};

export default BlockSpeed;
